import React, { useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";

import { useGetLocale } from "providers";

import coinImg from "./img.svg";
import coinImg_mobile from "./img_mobile.svg";
import css from "./style.module.scss";

export const HouseEdge = () => {
  const history = useHistory();
  const intl = useGetLocale();

  const rootRef = useRef<HTMLDivElement>(null);

  const handleClick = () => {
    history.push("house-edge-sharing-feyorra");
  };

  useEffect(() => {
    if (!rootRef.current) {
      return;
    }
    const observer = new ResizeObserver((entries) => {
      const { clientWidth: bodyWidth } = document.body;
      const { clientWidth: rootWidth } = entries[0].target;
      const { clientWidth: containerWidth } = entries[0].target
        .firstChild as HTMLDivElement;
      const img = entries[0].target.lastChild as HTMLImageElement;

      if (bodyWidth > 600) {
        img.src = coinImg;

        const imgPositionModifier = 260;
        const imgMoveValue = rootWidth - containerWidth - imgPositionModifier;
        img.style.right = `${Math.min(imgMoveValue, 0)}px`;
      } else {
        img.src = coinImg_mobile;

        const mobileImgPositionModifier = 120;
        const imgMoveValue =
          rootWidth - containerWidth - mobileImgPositionModifier;

        img.style.right = `${Math.min(imgMoveValue, 0)}px`;
      }
    });

    observer.observe(rootRef.current);

    const unobserve = () =>
      rootRef.current && observer.unobserve(rootRef.current);

    return () => {
      unobserve();
    };
  }, []);

  return (
    <div className={css.root} ref={rootRef}>
      <div className={css.container}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "feyorraUtilities.houseEdgeSharing" })}
        </h1>
        <div className={css.text}>
          <p>
            {intl.formatMessage({ id: "feyorraUtilities.edgeSharingText" })}
          </p>
          <p>
            {intl.formatMessage({ id: "feyorraUtilities.curSharing" })}
            <span>
              {intl.formatMessage(
                { id: "feyorraUtilities.totalVolume" },
                { percent: "0.25" },
              )}
            </span>
          </p>
        </div>
      </div>

      <span className={css.information} onClick={handleClick}>
        {intl.formatMessage({ id: "feyorraUtilities.moreInformation" })}
      </span>

      <img src={coinImg} alt="coinImg" className={css.img} />
    </div>
  );
};
