import React from "react";
import { useGetLocale } from "providers";

import css from "./style.module.scss";
import coinsImg from "./img.svg";

export const Header = () => {
  const intl = useGetLocale();
  return (
    <div className={css.root}>
      <div className={css.container}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "feyorraUtilities.feyorraUtilities" })}
        </h1>
        <span className={css.text}>
          {intl.formatMessage({ id: "feyorraUtilities.utilitiesText" })}
        </span>
      </div>

      <img src={coinsImg} alt="coinsImg" className={css.img} />
    </div>
  );
};
