import React from "react";
import { Helmet } from "react-helmet";

import { useGetLocale } from "providers";
import { Typography } from "components";

import { BetList } from "pages/Home/components/BetsList";
import { Header, HouseEdge, Wager } from "./components";
import css from "./style.module.scss";

const FeyorraUtilities = () => {
  const intl = useGetLocale();
  return (
    <>
      <Helmet>
        <title>Feyorra | Crypto Gambling Casino</title>
        <meta name="title" content=" Feyorra - Pasino.com" />
        <meta
          name="description"
          content="Feyorra is the utility token of Pasino.com and it comes with several perks. Stake your Fey and earn a passive income.  "
        />
        <meta
          name="keywords"
          content="Pasino Feyorra, Feyorra Utility, House Edge Sharing, Feyorra"
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>

      <Header />
      <div className={css.rowBanners}>
        <HouseEdge />
        <Wager />
      </div>

      <div className={css.bets_wrapper}>
        <Typography className={css.title} variant="h2">
          {intl.formatMessage({ id: "homeScreen.betsRow" })}
        </Typography>
        <BetList />
      </div>
    </>
  );
};

export default React.memo(FeyorraUtilities, () => true);
