import React from "react";
import { useHistory } from "react-router-dom";

import { useGetLocale } from "providers";

import img from "./img.webp";
import css from "./style.module.scss";

export const Wager = () => {
  const history = useHistory();
  const intl = useGetLocale();
  const handleClick = () => history.push("wager-mining");

  return (
    <div className={css.root}>
      <div className={css.container}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "feyorraUtilities.wagerMining" })}
        </h1>
        <p className={css.text}>
          {intl.formatMessage({ id: "feyorraUtilities.wagerMiningText" })}
        </p>
      </div>
      <span className={css.information} onClick={handleClick}>
        {intl.formatMessage({ id: "feyorraUtilities.moreInformation" })}
      </span>

      <img src={img} alt="img" className={css.img} />
    </div>
  );
};
